//React and Next Imports
import { cookies } from "next/headers";
//API
import {
  getProfilecountry,
  getProfilestate,
} from "@/api/serversideaction/category";
//Utils
import { GetToken } from "@/utilities/cookies";
//Components
import Myprofile from ".";

export default async function MyprofileServer({ searchParams }) {
  const [token, profiledatacountry, profilestate] = await Promise.all([
    GetToken(),
    getProfilecountry(),
    getProfilestate(),
  ]);

  const cookiesdata = await cookies();
  const verify = cookiesdata.get("verify");

  return (
    <Myprofile
      token={token}
      searchParams={searchParams}
      verify={verify}
      profiledatacountry={profiledatacountry}
      profilestate={profilestate}
    />
  );
}
